* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
}

p {
  line-height: 25px;
}

main {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: space-between;
}

main .content {
  background: linear-gradient(to top, #ff0, #00f);
}

@font-face {
  font-family: monRegular;
  src: url("../fonts/monRegular.otf");
}

body {
  background: #888;
  font-family: monRegular;
}

aside {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 20px;
}

#root {
  padding: 30px;
  background: #fefefe;
  max-width: 600px;
  margin: 50px auto;
  border-radius: 6px;
}

#root h1,
#root h3 {
  margin-bottom: 16px;
  letter-spacing: 2px;
}

#root p {
  text-align: justify;
  margin-bottom: 12px;
}

@media screen and (max-width: 768px) {
  main {
    display: unset;
    width: 100vw;
  }

  main .content {
    background: #ff2288;
    color: #ff0;
  }

  body {
    background: #1a1a1a;
  }

  body #root {
    max-width: 400px;
  }

  body #root h1 {
    color: #f0f;
  }
}